import React from 'react';
import {connect} from 'react-redux';
import './face-part-container.scss'
import classNames from 'classnames'

export default function FacePartContainer ({id, active, part, url, selectPart}) {
  return (
    <div className={classNames('FacePartContainer', active && 'active')} onClick={() => selectPart(id, part)}>
      <img className="one" src={url} alt=""/>
    </div>
  )
}
