import React from 'react';
import { connect } from 'react-redux'
import { randomizeFace, saveImage } from '../../reducers/preview'
import './button-actions.scss'

function ButtonActions({ randomizeFace, saveImage, current }) {
  return (
    <div className="ButtonActions">
      <button className="btn btn-primary btn-wide mb-2" onClick={randomizeFace}>
        Random
      </button>
      <br/>
      <button className="btn btn-primary btn-wide" onClick={saveImage}>
        Download
      </button>
    </div>
  )
}



export default connect(
  null,
  dispatch => ({
    randomizeFace: () => dispatch(randomizeFace()),
    saveImage: () => dispatch(saveImage())
  })
)(ButtonActions)
