import React from 'react';
import FacePartContainer from '../components/face-part-container'
import { connect } from 'react-redux'
import { changeColor } from '../reducers/preview'
import ColorPicker from '../components/color-picker'

function Base({items, current, changeColor, selectPart, part, noColors}) {
  return (
    <div>
      {items && items[part] && items[part].length && items[part].map(item => (
        <FacePartContainer
          active={current[part].id == item.id}
          key={item.id}
          id={item.id}
          part={part}
          url={item.value}
          selectPart={selectPart}
        />
      ))}
      <br/>
      { !noColors && <ColorPicker part={part} /> }
    </div>
  )
}

export default connect(
  state => ({
    items: state.partsData,
    current: state.current
  }),
  dispatch => ({
    changeColor: (part, color) => dispatch(changeColor(part, color)),
    selectPart: (id, part) => dispatch({ type: 'SELECT_PART', payload: {id, part} })
  })
)(Base)
