import React from 'react';
import {connect} from 'react-redux';
import './main-tabs.scss'
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs'
import Base from '../../face-parts/Base'

export default function MainTabs () {
  return (
    <div className="MainTabs">
      <Tabs>
        <TabList>
          <Tab>Head</Tab>
          <Tab>Ears</Tab>
          <Tab>Eyes</Tab>
        </TabList>

        <TabPanel>
          <Base part="head"/>
        </TabPanel>
        <TabPanel>
          <Base part="ears"/>
        </TabPanel>
        <TabPanel>
          <Base part="eyes" noColors={true}/>
        </TabPanel>
      </Tabs>
    </div>
  )
}
