import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';
console.log(API_URL);

const axiosInstance = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});

export function getFaceParts() {
  return axiosInstance.get(API_URL + 'face-parts');
}

export function saveImage(data) {
  //return axiosInstance.get('http://localhost:3001/' + 'save-image',);
  return axios({
    url: API_URL + 'save-image',
    method: 'GET',
    responseType: 'blob', // important,
    params: { data }
  })
}
