import React from 'react';
import { connect } from 'react-redux'
import {getCurrentHeadValue, getCurrentEarsValue, getCurrentEyesValue} from '../../reducers/preview'
import './preview.scss'

function Preview({head, ears, eyes}) {
  return (
    <div className="Preview">
      {ears && <img src={ears} alt=""/>}
      {head && <img src={head} alt=""/>}
      {eyes && <img src={eyes} alt=""/>}
    </div>
  )
}



export default connect(
  state => ({
    head: getCurrentHeadValue(state),
    ears: getCurrentEarsValue(state),
    eyes: getCurrentEyesValue(state)
  })
)(Preview)
