import { createSelector } from 'reselect'
import { changeImageColor } from '../services/canvas'
import * as backend from '../services/backend'
import { cloneDeep } from 'lodash'
import { saveAs } from 'file-saver';

const PARTS = ['head', 'ears', 'eyes']
const PARTS_WITH_COLORS = ['head', 'ears']

const initialState = {
  partsDataOriginal: {
    head: [],
    ears: [],
    eyes: []
  },
  partsData: {
    head: [],
    ears: []
  },
  current: {
    head: {
      id: null,
      color: null
    },
    ears: {
      id: null,
      color: null
    },
    eyes: {
      id: null,
      color: null
    }
  }
};

export function createReducer(initialState, handlers) {
  return function reducer(state = initialState, action) {
    if (handlers.hasOwnProperty(action.type)) {
      return handlers[action.type](state, action)
    } else {
      return state
    }
  }
}

// ACTIONS

export const loadFaceParts = () => (dispatch) => {
  dispatch({ type: 'LOAD_FACE_PARTS_REQUEST' });
  backend.getFaceParts().then(response => {
    dispatch({ type: 'LOAD_FACE_PARTS_SUCCESS', payload: response });
    setTimeout(() => {
      dispatch(randomizeFace());
    })
  }).catch(e => {
    dispatch({ type: 'LOAD_FACE_PARTS_FAILURE', payload: e });
  })
}

export const saveImage = () => (dispatch, getState) => {
  const data = getState().current;
  dispatch({ type: 'SAVE_IMAGE_REQUEST' });
  backend.saveImage(data).then(response => {
    saveAs(new Blob([response.data]), 'image' + Math.floor(Math.random() * 100000 + 1) + '.png');
    dispatch({ type: 'SAVE_IMAGE_SUCCESS', payload: response });

  }).catch(e => {
    dispatch({ type: 'SAVE_IMAGE_FAILURE', payload: e });
  })
}

export const changeColor = (part, color) => (dispatch, getState) => {
  getState().partsDataOriginal[part].forEach(el => {
    changeImageColor(el.value, color).then((value) => {
      dispatch({ type: 'SET_COLOR', payload: { id: el.id, part, value, color } })
    })
  })
}

export const randomizeFace = () => (dispatch, getState) => {

  PARTS_WITH_COLORS.forEach(part => {
    let r = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    let g = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    let b = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    dispatch(changeColor(part, '#' + r + g + b))
  })

  PARTS.forEach(part => {
    let index = Math.floor(Math.random() * getState().partsData[part].length)
    let id = getState().partsData[part][index].id
    dispatch({type: 'SELECT_PART', payload: {id, part }})
  })

}




// SELECTORS

export const getCurrentHeadValue = state => {
  if (!state.current.head.id) return null;

  return state.partsData.head.find(el => el.id == state.current.head.id).value;
}

export const getCurrentEarsValue = state => {
  if (!state.current.ears.id) return null;
  return state.partsData.ears.find(el => el.id == state.current.ears.id).value;
}

export const getCurrentEyesValue = state => {
  if (!state.current.eyes.id) return null;
  return state.partsData.eyes.find(el => el.id == state.current.eyes.id).value;
}

// REDUCER

const reducer = createReducer(initialState, {
  SELECT_PART: function(state, { payload: { id, part }}) {
    return {
      ...state,
      current: {
        ...state.current,
        [part]: {
          ...state.current[part],
          id: id,
        }
      }
    }
  },
  SET_COLOR: function(state, { payload: {id, part, value, color }}) {
    let newPart = [].concat(state.partsData[part])
    newPart.find(el => el.id == id).value = value;
    return {
      ...state,
      partsData: {
        ...state.partsData,
        [part]: newPart
      },
      current: {
        ...state.current,
        [part]: {
          ...state.current[part],
          color
        }
      }
    }
  },
  LOAD_FACE_PARTS_SUCCESS: function(state, { payload }) {
    return {
      ...state,
      partsData: cloneDeep(payload.data),
      partsDataOriginal: cloneDeep(payload.data)
    }
  }
});

export default reducer
