import React, { useState } from 'react';
import { connect } from 'react-redux'
import { changeColor } from '../../reducers/preview'
import './color-picker.scss'
import { ChromePicker } from 'react-color'
import { ReactComponent as Pipette } from './pipette.svg'

const colors = [
  '#84532a',
  '#653521',
  '#272b2a',
  '#ffd900',
  '#990000',
  '#e1cb9a',
  '#a94631',
  '#3399ff',
  '#ff99f4',
  '#006766'
]

function ColorPicker({ part, changeColor }) {
  const [showPicker, setShowPicker] = useState(false)
  return (
    <div className="ColorPicker">
      { colors.map(color => (
       <div className="color"
            style={{background: color}}
            onClick={() => changeColor(part, color)}>
       </div>
      ))}
      <div className="color"
           onClick={() => {setShowPicker(!showPicker)}}>
        <Pipette />
      </div>
      { showPicker && <ChromePicker className="chromePicker" disableAlpha={true} onChange={(color) => changeColor(part, color.hex)}/> }
    </div>
  )
}


export default connect(
  null,
  dispatch => ({
    changeColor: (part, color) => dispatch(changeColor(part, color)),
  })
)(ColorPicker)
